// Customize this 'myform.js' script and add it to your JS bundle.
// Then import it with 'import MyForm from "./myform.js"'.
// Finally, add a <MyForm/> element whereever you wish to display the form.

import React from "react";

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
        <form onSubmit={this.submitForm}
        action="https://formspree.io/f/xaylpdgb"
        method="POST">
        <div class="shadow overflow-hidden">
        <div class="px-4 py-5 bg-white sm:p-6">
            <div class="grid grid-cols-6 gap-6">
                <div class="col-span-6 sm:col-span-6">
                    <label for="name" class="block text-sm font-medium text-gray-700">Your name</label>
                    <input type="text" name="name" id="name" class="mt-1 focus:ring-gray-800 focus:border-gray-800 block w-full shadow-sm sm:text-sm border-gray-400 "/>
                </div>

                <div class="col-span-6 sm:col-span-6">
                    <label for="email_address" class="block text-sm font-medium text-gray-700">Email address</label>
                    <input type="email" name="email" id="email_address" autocomplete="email" class="mt-1 focus:ring-gray-800 focus:border-gray-800 block w-full shadow-sm sm:text-sm border-gray-400 "/>
                </div>

                <div class="col-span-6 sm:col-span-6">
                    <label for="message" class="block text-sm font-medium text-gray-700">Message</label>
                    <textarea type="text" name="message" id="message" class="mt-1 focus:ring-gray-800 focus:border-gray-800 block w-full shadow-sm sm:text-sm border-gray-400 "/>
                </div>
            </div>
        </div>
        <div class="px-4 py-3 bg-gray-50 text-left sm:px-6">
            {status === "SUCCESS" ? <p>Thanks!</p> : <button type="submit" class="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium  text-white bg-gray-400 hover:bg-yellow-300 hover:text-black focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-800">Send</button>}
            {status === "ERROR" && <p>Ooops! There was an error.</p>}
        </div>
        </div>
    </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}